___CSS_LOADER_EXPORT___.push([module.id, `.menu-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  flex-grow: 1;
}

.menu-piece {
  display: flex;
  flex-grow: 1;
  flex-wrap: wrap;
}

.menu-piece.reversed {
  flex-direction: row-reverse;
}

.dish {
  object-fit: cover;
  flex-basis: 30%;
  flex-shrink: 0;
  flex-grow: 1;
  width: 250px;
  height: 300px;
}

.menu-piece .dish-description {
  background-color: black;
  min-height: 100px;
  width: 30vw;
  margin: 10px 0;
  flex: 1 0 50%;
  align-self: center;
  color: white;
  z-index: 2;
}
.menu-piece .dish-description > h3 {
  font-size: 2.5rem;
}

.menu-piece.reversed > .dish-description {
  align-self: flex-start;
  text-align: right;
}
